<template>
  <div class="create-post-page">
    <h4>{{ isEditMode ? 'Edit Essay' : 'Create Essay' }}</h4>
    <uploader
      action="/upload"
      :beforeUpload="uploadCheck"
      :uploaded="uploadedData"
      @file-uploaded="handleFileUploaded"
      @file-uploaded-error="onFileUploadedError"
      class="d-flex align-items-center justify-content-center bg-light text-secondary w-100 my-4"
    >
      <h1>Click to upload</h1>
      <template #loading>
        <div class="d-flex">
          <div class="spinner-border text-secondary" role="status">
            <span class="sr-only">Loading...</span>
          </div>
          <h2>is Uploading...</h2>
        </div>
      </template>
      <template #uploaded="dataProps">
        <img :src="dataProps.uploadedData.data.url">
      </template>
    </uploader>
    <validate-form @form-submit="onFormSubmit">
      <div class="mb-3">
        <label class="form-label">Title: </label>
        <validate-input
          :rules="titleRules"
          v-model="titleVal"
          palceholder="Please enter the essay title"
          type='text'
        />
      </div>
      <div class="mb-3">
        <label class="form-label">Content: </label>
        <validate-input
          :rules="contentRules"
          v-model="contentVal"
          placeholder="Please enter the content"
          type="text"
          tag="textarea"
          rows='10'
        />
      </div>
      <template #submit>
          <button class="btn btn-primary btn-large">{{ isEditMode ? 'Edit' : 'Create' }}</button>
      </template>
    </validate-form>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, onMounted } from 'vue'
import { GlobalDataProps, PostProps, ResponseType, ImageProps } from '../store'
import { useStore } from 'vuex'
import { useRouter, useRoute } from 'vue-router'
import ValidateForm from '@/components/ValidateForm.vue'
import ValidateInput, { RulesProp } from '@/components/ValidateInput.vue'
import Uploader from '@/components/Uploader.vue'
import createMessage from '@/components/createMessage'
import { beforeUploadCheck } from '@/helper.ts'

export default defineComponent({
  name: 'CreatePost',
  components: {
    ValidateForm,
    ValidateInput,
    Uploader
  },
  setup() {
    const uploadedData = ref()
    const store = useStore<GlobalDataProps>()
    const router = useRouter()
    const route = useRoute()
    const isEditMode = !!route.query.id
    const titleVal = ref('')
    const contentVal = ref('')
    let imageId = ''
    const titleRules: RulesProp = [
      { type: 'required', message: 'The title can not be empty.' }
    ]
    const contentRules: RulesProp = [
      { type: 'required', message: 'The content can not be empty' }
    ]
    onMounted(() => {
      if (isEditMode) {
        store.dispatch('fetchPost', route.query.id).then((rawData: ResponseType<PostProps>) => {
          const currentPost = rawData.data
          if (currentPost.image) {
            uploadedData.value = { data: currentPost.image }
          }
          titleVal.value = currentPost.title
          contentVal.value = currentPost.content || ''
        })
      }
    })
    const handleFileUploaded = (rawData: ResponseType<ImageProps>) => {
      if (rawData.data._id) {
        imageId = rawData.data._id
      }
    }
    const onFormSubmit = (result: boolean) => {
      if (result) {
        const { column, _id } = store.state.user
        if (column) {
          const newPost: PostProps = {
            title: titleVal.value,
            content: contentVal.value,
            column,
            author: _id
          }
          if (imageId) {
            newPost.image = imageId
          }
          const actionName = isEditMode ? 'updatePost' : 'createPost'
          const sendData = isEditMode ? {
            id: route.query.id,
            payload: newPost
          } : newPost
          store.dispatch(actionName, sendData).then(() => {
            createMessage('Post successfully!', 'success')
            setTimeout(() => {
              router.push({ name: 'column', params: { id: column } })
            }, 2000)
          })
        }
      }
    }
    const onFileUploadedError = (rawData: ResponseType<ImageProps>) => {
      createMessage(`image uploaded failed, ${rawData.msg}`, 'error')
    }
    const uploadCheck = (file: File) => {
      const result = beforeUploadCheck(file, { format: ['image/jpeg', 'image/png'], size: 1 })
      const { passed, error } = result
      if (error === 'format') {
        createMessage('only JPG/PNG format images allowed to upload', 'error')
      }
      if (error === 'size') {
        createMessage('The size is limited to 1 Mb', 'error')
      }
      return passed
    }
    return {
      titleVal,
      contentVal,
      titleRules,
      contentRules,
      onFormSubmit,
      handleFileUploaded,
      onFileUploadedError,
      uploadCheck,
      uploadedData,
      isEditMode
    }
  }
})
</script>

<style>
.create-post-page .file-upload-container {
  height: 200px;
  cursor: pointer;
}

.create-post-page .file-upload-container img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
</style>
