<template>
  <div class="post-detail-page">
    <modal title="delete essay" :visible="modalIsVisible"
      @modal-on-close="modalIsVisible = false"
      @modal-on-confirm="hideAndDelete">
      <p>Are you sure to delete it?</p>
    </modal>
    <article class="w-75 mx-auto mb-5 pb-3" v-if="currentPost">
      <img :src="currentImageUrl" :alt="currentPost.title" class="rounded-lg img-fluid my-4" v-if="currentImageUrl">
      <h2 class="mb-4">{{ currentPost.title }}</h2>
      <div class="user-profile-component border-top border-bottom py-3 mb-5 align-items-center row g-0">
        <div class="col">
          <user-profile :user="currentPost.author" v-if="typeof currentPost.author === 'object'"></user-profile>
        </div>
        <span class="text-muted col text-right font-italic">released at: {{ currentPost.createdAt }}</span>
      </div>
      <div v-html="currentHTML"></div>
      <div v-if="showEditArea" class="btn-group mt-5">
        <router-link :to="{ name: 'create', query: { id: currentPost._id } }" type="button" class="btn btn-success">Edit</router-link>
        <button type="button" class="btn btn-danger" @click.prevent="modalIsVisible = true">Delete</button>
      </div>
    </article>
  </div>

</template>

<script lang="ts">
import { defineComponent, onMounted, computed, ref } from 'vue'
import UserProfile from '@/components/UserProfile.vue'
import { useStore } from 'vuex'
import { useRoute, useRouter } from 'vue-router'
import { GlobalDataProps, PostProps, ImageProps, UserProps, ResponseType } from '../store'
import Modal from '@/components/Modal.vue'
import createMessage from '@/components/createMessage.ts'

export default defineComponent({
  name: 'post-detail',
  components: {
    UserProfile,
    Modal
  },
  setup() {
    const store = useStore<GlobalDataProps>()
    const route = useRoute()
    const router = useRouter()
    const modalIsVisible = ref(false)
    const currentId = route.params.id
    onMounted(async () => {
      try {
        await store.dispatch('fetchPost', currentId)
      } catch (e) {
        console.log(e)
      }
    })
    const currentPost = computed<PostProps>(() => store.getters.getPostById(currentId))
    const currentHTML = computed(() => {
      // if use markdown
      // if (currentPost.value && currentPost.value.content) {
      //   return md.render(currentPost.value.content)
      // }
      return currentPost.value.content
    })
    const showEditArea = computed(() => {
      const { isLogin, _id } = store.state.user
      if (currentPost.value && currentPost.value.author && isLogin) {
        const postAuthor = currentPost.value.author as UserProps
        return postAuthor._id === _id
      } else {
        return false
      }
    })
    const currentImageUrl = computed(() => {
      if (currentPost.value && currentPost.value.image) {
        const { image } = currentPost.value
        return (image as ImageProps).url + '?x-oss-process=image/resize,w_850'
      } else {
        return null
      }
    })
    const hideAndDelete = async () => {
      modalIsVisible.value = false
      try {
        const rawData: ResponseType<PostProps> = await store.dispatch('deletePost', currentId)
        createMessage('Congradulations, delete successfully!!', 'success', 2000)
        setTimeout(() => {
          router.push({
            name: 'column',
            params: {
              id: rawData.data.column
            }
          })
        }, 2000)
      } catch (err) {
        console.log(err)
      }
    }
    return {
      currentPost,
      currentImageUrl,
      currentHTML,
      showEditArea,
      modalIsVisible,
      hideAndDelete
    }
  }
})
</script>

